.pointBoxWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: stretch;
  .boxBody {
    margin: 0px 2px;
    width: 5px;
    height: 5px;
    border: #999 solid 1px;
    border-radius: 2px;
    .boxValue {
      background: #999;
      height: 5px;
    }
  }
}