@import "~@material/typography/mdc-typography";
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  align-self: center;
  margin: 0px 8px;
  .header {
    @include mdc-typography('overline');
    font-size: 14px;
    border-bottom: #333 solid 3px;
  }
  .content,
  .contentLanguage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    align-self: stretch;
    padding-top: 16px;
    max-width: 475px;
    min-width: 320px;
    margin-top: 16px;
    .contentLanguage {
      max-width: 320px;
      flex-wrap: wrap;
    }
    .skillItem,
    .skillItemLanguage {
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      align-items: center;
      flex-wrap: wrap;
      align-self: stretch;
      @include mdc-typography('overline');
      .title,
      .point {
        display: flex;
        flex: 1;
        flex-wrap: wrap;
      }
      .point {
        justify-content: flex-end;
        align-items: center;
      }
    }
  }
  @media screen and (max-width: 475px) {
    .container {
      margin-bottom: 16px;
    }
    .content {
      min-width: 300px;
      .skillItemLanguage {
        flex-direction: column;
        align-items: center;
        .point {
          display: flex;
          justify-content: space-between;
          align-self: stretch;
        }
      }
    }
    .header {
      margin-top: 64px;
    }
  }
}