@import "~@material/typography/mdc-typography";
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  border: #eee solid 1px;
  margin: 32px;
  margin-bottom: 64px;
  border-radius: 5px;
  flex-direction: column;
  padding: 16px;
  .header {
    @include mdc-typography('overline');
    font-size: 14px;
    border-bottom: 3px solid #333;
    color: #333;
  }
  .content {
    @include mdc-typography('body1');
    margin-top: 16px;
    font-size: 14px;
    text-align: left;
    max-width: 475px;
    text-indent: 2em;
    font-weight: 400;
    color: #333;
    @media screen and (max-width: 475px) {
      padding:0px 16px;
    }
    p {
      margin-top: 8px;
      padding-top: 0px;
    }
  }
}