.detail {
  display: flex;
  align-self: stretch;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  .expAndSkill {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-self: center;
    flex-wrap: wrap; // border: #eee solid 1px;
    padding: 16px 32px;
    border-radius: 5px;
    margin: 0px 64px;
    margin-bottom: 32px;
    @media screen and (max-width: 475px) {
      padding: 0px 16px;
      margin-bottom: 16px;
    }
  }
}