@import "~@material/typography/mdc-typography";
.header {
  display: flex;
  align-self: stretch;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 54px;
  .photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: #eee;
    object-fit: cover;
    img {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      object-fit: cover;
    }
  }
  .caption {
    @include mdc-typography('overline');
    margin-top: 16px;
    color: #999;
  }
  .minCaption {
    @include mdc-typography('overline');
    color: #999;
    font-size: 11px;
  }
}