@import "~@material/typography/mdc-typography";
.container {
  background: #fff;
  color: #333;
  .menu-header {
    position: fixed;
    top: 0;
    left: 0;
    height: 74px;
    width: 100%;
    z-index: 3;
    border-bottom: #eee solid 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: white;
    a {
      @include mdc-typography('overline');
      margin: 0px 16px;
      font-size: 14px;
      text-decoration: none;
      color: #333;
      &:hover {
        border-bottom: #333 solid 3px;
        cursor: pointer;
      }
    }
  }
  .content {
    padding-top: 64px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    .header {
      display: flex;
      align-self: stretch;
      justify-content: center;
      align-items: center;
      height: 240px;
      flex-direction: column;
      .photo {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background: #eee;
      }
      .caption {
        @include mdc-typography('overline');
        margin-top: 16px;
        color: #999;
      }
      img {
        width: 100%;
        object-fit: cover;
        opacity: .64;
      }
    }
  }
}