@import "~@material/typography/mdc-typography";
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  align-self: center;
  margin-bottom: 64px;
  min-width: 300px;
  border: #eee solid 1px;
  border-radius: 5px;
  padding: 16px 8px;
  margin-top: 64px;
  .header {
    @include mdc-typography('overline');
    font-size: 14px;
    border-bottom: #333 solid 3px;
  }
  .content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    align-self: stretch;
    padding-top: 16px;
    .contractItem {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      align-self: stretch;
      margin-top: 16px;
      a {
        text-decoration: none;
        display: flex;
        flex: 1;
        justify-content: space-between;
        flex-direction: row;
        align-items: flex-end;
        color: #333;
        font-size: 32px;
        i {
          margin-right: 16px;
          padding: 8px;
        }
      }
    }
  }
  @media screen and (max-width: 475px) {
    min-width: inherit;
    padding-left: 32px;
    padding-right: 32px;
  }
}