@import "~@material/typography/mdc-typography";
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  align-self: flex-start;
  margin: 0px 8px;
  .header {
    @include mdc-typography('overline');
    font-size: 14px;
    border-bottom: #333 solid 3px;
  }
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    align-self: stretch;
    padding-top: 16px;
    .experienceItem {
      @include mdc-typography('overline');
      align-self: stretch;
      display: flex;
      justify-content: space-around;
      align-items: flex-start;
      span {
        flex: 1;
        margin-top: 8px;
        padding: 8px;
        b {
          @include mdc-typography('overline');
          font-size: 11px;
          color: #333;
          font-weight: bold;
        }
      }
      .companyDetail {
        flex: 1;
      }
    }
  }
}